* {
  margin: 0;
  padding: 0;
  -moz-user-select: none;
  -khtml-user-select: none;
  user-select: none;
}
form,
ul,
ol,
li,
dl,
dt,
dd,
h1,
h2,
h3,
h4,
h5,
p {
  list-style: none outside none;
}
a {
  text-decoration: none;
  color: #ccc;
  outline: none;
}
a:hover {
  text-decoration: underline;
  color: #1298F4;
}
a img {
  border: none;
}
.fr {
  float: right;
}
.fl {
  float: left;
}
body {
  font: 12px/1.8 Microsoft YaHei, simsong;
  height: 100%;
  overflow: hidden;
  background: #39a1d1;
}
.bgloader {
  background: url(../images/loading.gif) no-repeat #fff center;
  z-index: 10000;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
#task-bar {
  position: fixed;
  right: 0;
  bottom: 0;
  left: 4%;
  width: 92%;
  height: 36px;
  z-index: 1010;
  background: url("../images/dock_bg.png") repeat-x;
}
#task-bar .task-bar-container {
  position: relative;
  width: 97%;
  overflow: hidden;
  top: -21px;
}
#task-bar #task-bar-left {
  position: absolute;
  right: 0;
  bottom: 0;
  left: -30px;
  width: 30px;
  height: 36px;
  overflow: hidden;
  background: url("../images/dock_bg_left.png") no-repeat;
}
#task-bar #task-bar-right {
  position: absolute;
  right: -30px;
  bottom: 0;
  width: 30px;
  height: 36px;
  overflow: hidden;
  background: url("../images/dock_bg_right.png") no-repeat;
}
.task-window-container {
  width: 99999px;
  overflow: hidden;
  margin-left: 137px;
}
.task-btn {
  position: absolute;
  bottom: 1px;
  display: block;
  background: transparent;
  border: none;
  overflow: hidden;
  outline: none;
  cursor: pointer;
  visibility: hidden;
  z-index: 1500;
}
.task-btn .task-icon {
  width: 16px;
  height: 16px;
  margin-top: 4px;
  display: inline-block;
}
.task-btn .task-icon-left {
  background: url('../images/dock_btn_left.png');
}
.task-btn .task-icon-right {
  background: url('../images/dock_btn_right.png');
}
.task-btn-left {
  left: 112px;
  top: -15px;
}
.task-btn-right {
  right: 0;
  top: -15px;
}
.task-window {
  float: left;
}
.task-window li {
  position: relative;
  float: left;
  width: 42px;
  height: 42px;
  margin-right: 16px;
  overflow: hidden;
  color: #F4F4F4;
  cursor: pointer;
}
.task-window b {
  display: block;
  font-weight: normal;
  opacity: .7;
}
.task-window b:hover {
  opacity: 1;
}
.task-window b:active {
  opacity: 1;
}
.task-window b.focus {
  opacity: 1;
}
.task-window img {
  position: absolute;
  top: 0;
  left: 0;
  width: 42px;
  height: 42px;
  border-radius: 3px;
}
.task-window span {
  display: none;
  height: 24px;
  padding-left: 25px;
  line-height: 25px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.desktop-view {
  background-position: 0 0;
}
.desktop-view:hover {
  background-position: -23px 0;
}
.desktop-view:active {
  background-position: -46px 0;
}
#desk {
  position: absolute;
  top: 0;
  bottom: 60px;
  width: 100%;
  overflow: hidden;
  *z-index: 2;
}
#desk ul {
  width: 100%;
  height: 100%;
  overflow: hidden;
  position: absolute;
}
#desk ul li {
  padding: 1em 1.5em;
  text-align: center;
  position: absolute;
  cursor: pointer;
}
#desk ul li img {
  position: relative;
  z-index: 1;
  border-radius: 3px 3px 3px 3px;
  display: block;
  height: 54px;
  width: 54px;
  margin: auto;
}
#desk ul li span {
  background: none repeat scroll 0 0 rgba(0, 0, 0, 0.3);
  filter: none;
  border-radius: 15px;
  width: 54px;
  height: 18px;
  color: #fff;
  display: inline-block;
  margin-top: 10px;
  position: relative;
  text-align: center;
  text-overflow: ellipsis;
  z-index: 1;
  padding: 2px 5px;
  font-size: 11px;
  word-break: break-all;
  white-space: nowrap;
  overflow: hidden;
  line-height: 1rem;
}
#desk ul li:hover span,
#desk ul li.hover span {
  background: none;
}
#desk ul li.focus span {
  background: none;
}
#desk ul li em {
  display: none;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 0;
  width: 100%;
  height: 100%;
  -webkit-border-radius: 5px;
  -moz-border-radius: 5px;
  border-radius: 5px;
  background: none repeat scroll 0 0 rgba(0, 0, 0, 0.5);
  opacity: 0.5;
  filter: alpha(opacity=12);
}
#desk ul li:hover em,
#desk ul li.hover em {
  display: block;
}
#desk ul li.focus em {
  display: block;
  opacity: 0.25;
  filter: alpha(opacity=25);
}
#desk ul li,
#desk ul li span,
.title-bar {
  -moz-user-select: none;
  -khtml-user-select: none;
  user-select: none;
}
.window-container {
  position: absolute;
  padding: 0;
  overflow: hidden;
  border-radius: 3px;
  /*background: #656672;
  box-shadow: 0 1px 5px rgba(0, 0, 0, 0.4)*/
}
.window-current {
  /*background: #363740;*/
  box-shadow: 0 1px 10px rgba(0, 0, 0, 0.4);
}
.title-bar {
  background: url(../images/titlebar_bg_cur.png) repeat-x;
  position: relative;
  z-index: 10;
  height: 26px;
  padding: 0 20px;
  line-height: 26px;
  overflow: hidden;
  color: #393836;
  font-weight: bold;
  border-bottom: 1px solid #CCC;
  border-radius: 3px 3px 0 0;
}
.title-icon {
  position: absolute;
  top: 5px;
  left: 5px;
  width: 16px;
  height: 16px;
  border-radius: 3px;
}
.title-info {
  padding-left: 5px;
}
.title-handle {
  overflow: hidden;
  position: absolute;
  right: 5px;
  top: 4px;
}
.title-handle a {
  float: left;
  width: 19px;
  height: 21px;
  margin-left: 5px;
  line-height: 10;
  overflow: hidden;
  background: url(../images/sprite_main_png.png) no-repeat;
}
.title-handle a.ha-fresh {
  background-position: 1px -59px;
}
.title-handle a.ha-fresh:hover {
  background-position: 1px -30px;
}
.title-handle a.ha-min {
  background-position: -26px -58px;
}
.title-handle a.ha-min:hover {
  background-position: -26px -30px;
}
.title-handle a.ha-max {
  background-position: -55px -59px;
}
.title-handle a.ha-max:hover {
  background-position: -55px -30px;
}
.title-handle a.ha-revert {
  background-position: -112px -59px;
}
.title-handle a.ha-revert:hover {
  background-position: -112px -30px;
}
.title-handle a.ha-close {
  background-position: -85px -59px;
}
.title-handle a.ha-close:hover {
  background-position: -85px -30px;
}
.window-frame {
  position: absolute;
  top: 25px;
  right: 0;
  bottom: 2px;
  *bottom: 0;
  left: 0;
  background: #FFF;
  width: 100%;
}
.window-frame iframe {
  position: absolute;
  border: 0;
  height: 100%;
  width: 100%;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}
.set-bar {
  _width: 100%;
  position: absolute;
  background: #E8E8E8;
  border-top: 1px solid #b9b9b9;
  bottom: 0;
  height: 30px;
  left: 2px;
  right: 2px;
  bottom: 2px;
}
.set-bar .btn {
  margin-top: 3px;
  margin-right: 5px;
  border: 1px solid #E8E8E8;
  border-radius: 3px;
  color: #7D7D7D;
  cursor: pointer;
  display: inline-block;
  height: 20px;
  padding: 1px;
  position: relative;
}
.set-bar .btn:hover {
  border: 1px solid #7D7D7D;
  background: #fff;
  color: #333;
  text-decoration: none;
}
.set-bar .btn .icon {
  left: 8px;
  position: absolute;
  top: 3px;
}
.set-bar .btn .btn-con {
  height: 20px;
  line-height: 21px;
  overflow: hidden;
  padding: 0 8px;
}
.popup-menu {
  border: 1px solid #AEAEAE;
  box-shadow: 0 0 6px rgba(0, 0, 0, 0.4);
  position: absolute;
  width: 125px;
}
.popup-menu ul {
  padding: 1px;
  position: relative;
}
.popup-menu ul li {
  height: 24px;
  position: relative;
  vertical-align: middle;
}
.popup-menu a {
  background-position: 100px 100px;
  border-radius: 2px;
  color: #333333;
  display: block;
  height: 24px;
  line-height: 24px;
  overflow: hidden;
  padding-left: 35px;
}
.popup-menu a:hover,
.popup-menu a.focus {
  background-color: #3B7CE6;
  background-position: 0 -780px;
  color: #FFFFFF;
  text-decoration: none;
}
.smart_menu_box {
  display: none;
  width: 140px;
  position: absolute;
  z-index: 201105;
}
.smart_menu_box .smart_menu_body {
  padding: 1px;
  border: 1px solid #B8CBCB;
  background-color: #fff;
  -moz-box-shadow: 2px 2px 5px #666;
  -webkit-box-shadow: 2px 2px 5px #666;
  box-shadow: 2px 2px 5px #666;
}
.smart_menu_box .smart_menu_ul {
  margin: 0;
  padding: 0;
  list-style-type: none;
}
.smart_menu_box .smart_menu_li {
  position: relative;
}
.smart_menu_box .smart_menu_a {
  display: block;
  height: 25px;
  line-height: 24px;
  padding: 0 5px 0 25px;
  color: #000;
  font-size: 12px;
  text-decoration: none;
  overflow: hidden;
}
.smart_menu_box .smart_menu_a:hover,
.smart_menu_box .smart_menu_a_hover {
  background-color: #348CCC;
  color: #fff;
  text-decoration: none;
}
.smart_menu_box .smart_menu_li_separate {
  line-height: 0;
  margin: 3px;
  border-bottom: 1px solid #B8CBCB;
  font-size: 0;
}
.smart_menu_box .smart_menu_triangle {
  width: 0;
  height: 0;
  border: 5px dashed transparent;
  border-left: 5px solid #666;
  overflow: hidden;
  position: absolute;
  top: 7px;
  right: 5px;
}
.smart_menu_box .smart_menu_a:hover .smart_menu_triangle,
.smart_menu_box .smart_menu_a_hover .smart_menu_triangle {
  border-left-color: #fff;
}
.smart_menu_box .smart_menu_li_hover .smart_menu_box {
  top: -1px;
  left: 130px;
}
#start-menu {
  position: absolute;
  bottom: 40px;
  left: 1px;
  z-index: 1000;
  box-shadow: 0 1px 10px rgba(0, 0, 0, 0.4);
  border-radius: 3px;
  display: none;
  background: #fff;
}
.start-menu {
  float: left;
  padding: 15px;
}
.start-menu li {
  line-height: 28px;
  border: solid 1px #fff;
  margin: 3px 0;
}
.start-menu li.sep {
  height: 1px;
  background: #ababab;
  width: 50px;
  margin: 6px auto;
  border: none;
}
.start-menu li.sep:hover {
  border: none;
}
.start-menu li:hover {
  border: solid 1px #ccc;
  border-radius: 2px;
  background: #eee;
  background: -webkit-gradient(linear, left top, left bottom, color-stop(0.39, #f7f7f7), color-stop(0, #fafafa), color-stop(0.94, #f0f0f0));
  background: -moz-linear-gradient(center top, #f7f7f7 39%, #fafafa 0%, #f0f0f0 94%);
}
.start-menu li a,
.start-menu li span {
  color: #000;
  display: block;
  padding: 0 0 0 30px;
  width: 80px;
  text-shadow: 0 0 1px #ddd, 0 0 0 #000;
  cursor: pointer;
}
.startMenuBtn {
  display: block;
  cursor: pointer;
  width: 42px;
  height: 42px;
  position: absolute;
  z-index: 1000;
  bottom: 15px;
  left: 1%;
  background: url("../images/task-icon.png") no-repeat center;
}
.startMenuBtn .task-bar-line {
  width: 1px;
  background: #808080;
  height: 35px;
  position: absolute;
  right: -80%;
  bottom: -15px;
  transform: skew(-25deg);
  -ms-transform: skew(-25deg);
  /* IE 9 */
  -moz-transform: skew(-25deg);
  /* Firefox */
  -webkit-transform: skew(-25deg);
  /* Safari 和 Chrome */
  -o-transform: skew(-25deg);
}
.resize {
  position: absolute;
}
.resize.resize-t {
  left: 0;
  top: -3px;
  width: 100%;
  height: 5px;
  z-index: 20;
  cursor: n-resize;
}
.resize.resize-r {
  right: -3px;
  top: 0;
  width: 5px;
  height: 100%;
  z-index: 1;
  cursor: e-resize;
}
.resize.resize-b {
  left: 0;
  bottom: -3px;
  width: 100%;
  height: 5px;
  z-index: 1;
  cursor: s-resize;
}
.resize.resize-l {
  left: -3px;
  top: 0;
  width: 5px;
  height: 100%;
  z-index: 1;
  cursor: w-resize;
}
.resize.resize-rt {
  right: -3px;
  top: -3px;
  width: 10px;
  height: 10px;
  z-index: 2;
  cursor: ne-resize;
}
.resize.resize-rb {
  right: -3px;
  bottom: -3px;
  width: 10px;
  height: 10px;
  z-index: 2;
  cursor: se-resize;
}
.resize.resize-lt {
  left: -3px;
  top: -3px;
  width: 10px;
  height: 10px;
  z-index: 2;
  cursor: nw-resize;
}
.resize.resize-lb {
  left: -3px;
  bottom: -3px;
  width: 10px;
  height: 10px;
  z-index: 2;
  cursor: sw-resize;
}
.webos-background {
  position: absolute;
  top: 0;
  left: 0;
  z-index: -2;
  width: 100%;
  height: 100%;
  background-size: cover;
  background-position: center;
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
}
#dock-tips-container {
  background: none repeat scroll 0 0 rgba(0, 0, 0, 0.3);
  filter: none;
  border-radius: 15px;
  height: 18px;
  color: #fff;
  display: inline-block;
  position: absolute;
  text-align: center;
  text-overflow: ellipsis;
  z-index: 2100;
  padding: 2px 15px;
  font-size: 11px;
  word-break: break-all;
  white-space: nowrap;
  overflow: hidden;
  line-height: 1rem;
}
.webos-card {
  position: absolute;
  top: 50px;
  display: table;
  right: 20px;
  vertical-align: middle;
  padding: 10px 25px;
  width: 270px;
  border-radius: 25px;
  height: 95px;
  border: 2px solid rgba(187, 186, 186, 0.5);
  background: rgba(101, 101, 101, 0.48);
  color: #fff;
}
.webos-card:hover {
  cursor: all-scroll;
}
.webos-card:hover .card-close {
  display: block;
}
.webos-card .card-close {
  display: none;
  position: absolute;
  top: 0;
  right: 10px;
  text-align: center;
  padding: 5px;
  cursor: pointer;
}
.webos-card .card-close:hover {
  font-weight: 700;
}
.webos-card .thumbnail {
  height: 88px;
  width: 92px;
  display: table-cell;
  vertical-align: middle;
}
.webos-card .thumbnail img {
  width: 64px;
  height: 64px;
  border-radius: 50%;
  vertical-align: middle;
}
.webos-card .content {
  display: table-cell;
  vertical-align: middle;
}
.webos-card .content h3 {
  display: inline-block;
  padding-right: 15px;
  font-weight: 800;
  font-size: 16px;
}
.webos-card .content p {
  font-size: 14px;
}
.webos-card .content p span {
  font-size: 12px;
  padding-right: 5px;
}



